.modal_background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.25);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.modal_card {
  background: white;
  padding: 25px;
  border: 3px solid black;
  border-radius: 5px;
}
